import { ChangeDetectionStrategy, Component, EventEmitter, OnInit, Output, Type, inject } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { QuestionService } from '../../../../services/question.service';
import { IQuestion } from '../../../../models/question';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AbstractControl, FormArray, FormBuilder, FormControl, FormGroup, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { IOption } from '../../../../models/option';

export const atLeastOneIsAnswer: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
  const options = control.get('options') as FormArray;
  if (options) {
    const hasAtLeastOneAnswer = options.controls.some(option => option.get('isAnswer')?.value);
    return hasAtLeastOneAnswer ? null : { atLeastOneIsAnswer: true };
  }
  return null;
};


@Component({
	selector: 'ngbd-modal-confirm',
	standalone: true,
	template: `
		<div class="modal-header">
			<h4 class="modal-title" id="modal-title">Eliminación de pregunta</h4>
			<button
				type="button"
				class="btn-close"
				aria-describedby="modal-title"
				(click)="modal.dismiss('Cross click')"
			></button>
		</div>
		<div class="modal-body">
			<p>
        <strong>¿Estás seguro de querer eliminar la pregunta?</strong>
			</p>
			<p>
        Toda la información asociada a esta pregunta se eliminará permanentemente.
				<span class="text-danger">Esta operación no se puede deshacer.</span>
			</p>
		</div>
		<div class="modal-footer">
			<button type="button" class="btn btn-outline-secondary" (click)="modal.dismiss('cancel click')">Cancelar</button>
			<button type="button" class="btn btn-danger" (click)="confirm()">Eliminar pregunta</button>
		</div>
	`,
})
export class NgbdModalConfirm {
	@Output() confirmDelete = new EventEmitter<void>();
  modal = inject(NgbActiveModal);

  confirm() {
    this.confirmDelete.emit();
    this.modal.close('Ok click');
  }
}

export class NgbdModalConfirmAutofocus {
	modal = inject(NgbActiveModal);
}

const MODALS: { [name: string]: Type<any> } = {
	focusFirst: NgbdModalConfirm,
	autofocus: NgbdModalConfirmAutofocus,
};

@Component({
  selector: 'app-question',
  templateUrl: './question.component.html',
  styleUrl: './question.component.scss',
})
export class QuestionComponent implements OnInit {
  questionForm: FormGroup;
  questionOptions: IOption[] = [];

  submitted: boolean = false;
  loading: boolean = false;

  typeOfQuestion: number;
  typeOfOptions: number;
  numberOfOptions: number;
  questionId: string;

  constructor(
    private fb: FormBuilder,
    private questionService: QuestionService,
    private router: Router,
    private route: ActivatedRoute
  ) {
    this.questionForm = this.fb.group({
      title: new FormControl('', [Validators.required, Validators.minLength(10)]),
      difficulty: new FormControl('1', [Validators.required, Validators.min(1), Validators.max(10)]),
      numberOfOptions: new FormControl('', [Validators.required]),
      typeOfQuestion: new FormControl(this.typeOfQuestion, [Validators.required]),
      options: new FormArray([]),
      typeOfOptions: new FormControl(this.typeOfOptions, [Validators.required])
    }, { validators: atLeastOneIsAnswer });
  }

  get f() { return this.questionForm.controls; }

  get opts() {
    return this.f['options'] as FormArray;
  }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.questionId = params['id'];
      this.loadQuestion();
    });
  }

  loadQuestion() {
    this.questionService.getQuestionById(this.questionId).valueChanges().subscribe((question: IQuestion) => {
      if (question) {
        this.questionForm.patchValue(question);
        this.typeOfQuestion = question.typeOfQuestion;
        this.typeOfOptions = question.typeOfOptions;
        this.numberOfOptions = question.numberOfOptions;

        this.opts.clear();
        for (let option of question.options) {
          this.opts.push(this.fb.group({
            image: [option.image, this.typeOfOptions == 2 ? Validators.required : null],
            isAnswer: [option.isAnswer, Validators.required],
            text: [option.text, this.typeOfOptions == 1 ? Validators.required : null],
          }));
        }

        // Aggiungi questionImage se typeOfQuestion è 2
        if (this.typeOfQuestion == 2) {
          this.questionForm.addControl('questionImage', this.fb.control(question.questionImage, Validators.required));
        } else {
          this.questionForm.addControl('questionText', this.fb.control(question.questionText, Validators.required));
        }
      }
    });
  }

  onChangeOptions(e) {
    this.numberOfOptions = e.target.value || 0;

    if (this.opts.length < this.numberOfOptions) {
      for (let i = this.opts.length; i < this.numberOfOptions; i++) {
        this.opts.push(this.fb.group({
          image: ['', this.typeOfOptions == 2 ? Validators.required : null],
          isAnswer: [false, Validators.required],
          text: ['', this.typeOfOptions == 1 ? Validators.required : null],
        }));
      }
    } else {
      for (let i = this.opts.length - 1; i >= this.numberOfOptions; i--) {
        this.opts.removeAt(i);
      }
    }
  }

  onChangeTypeOfOption(e) {
    this.typeOfOptions = +e.target.value || 0;
  }

  onChangeTypeOfQuestion(e) {
    this.typeOfQuestion = +e.target.value || 0;

    if (this.typeOfQuestion == 1) {
      if (this.questionForm.get('questionImage')) {
        this.questionForm.removeControl("questionImage");
      }
      this.questionForm.addControl('questionText', this.fb.control('', Validators.required));
    } else if (this.typeOfQuestion == 2) {
      if (this.questionForm.get('questionText')) {
        this.questionForm.removeControl("questionText");
      }
      this.questionForm.addControl('questionImage', this.fb.control('', Validators.required));
    }
  }

  onChangeQuestionImage(event) {
    if (event.target.files.length > 0) {
      const fileReader = new FileReader();

      fileReader.onload = () => {
        this.questionForm.controls['questionImage'].setValue(fileReader.result);
      };

      fileReader.onerror = (error) => {
        console.error('Error reading file:', error);
      };

      fileReader.readAsDataURL(event.target.files[0]);
    } else {
      this.questionForm.controls['questionImage'].setValue('');
    }
  }

  deleteQuestionImage() {
    this.questionForm.controls['questionImage'].setValue('');
  }

  onChangeOptionImages(event, index) {
    if (event.target.files && event.target.files.length === 0) {
      this.opts.at(index).get('image').patchValue('');
      return;
    }

    const fileReader: FileReader = new FileReader();

    fileReader.readAsDataURL(event.target.files[0]);

    fileReader.onload = () => {
      this.opts.at(index).get('image').patchValue(fileReader.result);
    }
  }

  deleteOptionImage(index) {
    this.opts.at(index).get('image').patchValue('');
  }

  onSubmit() {
    this.submitted = true;

    if (this.questionForm.invalid) {
      return;
    }

    this.questionService.updateQuestion(this.questionId, this.questionForm.value);
    this.router.navigate(['question/list']);
  }

  onReset() {
    this.submitted = false;
    this.questionForm.reset();
    this.opts.clear();
  }

  onClear() {
    this.submitted = false;
    this.opts.reset();
  }

  private modalService = inject(NgbModal);
	open(name: string) {
    const modalRef = this.modalService.open(MODALS[name]);
    modalRef.componentInstance.confirmDelete.subscribe(() => this.deleteQuestion());
  }
  deleteQuestion() {
    if (this.questionId) {
      this.questionService.deleteQuestion(this.questionId).then(() => {
        this.router.navigate(['/question/list']);
      }).catch((error) => {
        console.error("Error deleting question: ", error);
      });
    }
  }
}
