import { ChangeDetectionStrategy, Component, EventEmitter, OnInit, Output, Type, inject } from '@angular/core';
import { IQuestion } from '../../../../models/question';
import { QuestionService } from '../../../../services/question.service';
import { orderBy } from 'lodash';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';

@Component({
  selector: 'ngbd-modal-confirm',
  standalone: true,
  template: `
    <div class="modal-header">
      <h4 class="modal-title" id="modal-title">Eliminación de pregunta</h4>
      <button
        type="button"
        class="btn-close"
        aria-describedby="modal-title"
        (click)="modal.dismiss('Cross click')"
      ></button>
    </div>
    <div class="modal-body">
      <p>
        <strong>¿Estás seguro de querer eliminar la pregunta?</strong>
      </p>
      <p>
        Toda la información asociada a esta pregunta se eliminará permanentemente.
        <span class="text-danger">Esta operación no se puede deshacer.</span>
      </p>
    </div>
    <div class="modal-footer">
      <button type="button" class="btn btn-outline-secondary" (click)="modal.dismiss('cancel click')">Cancelar</button>
      <button type="button" class="btn btn-danger" (click)="confirm()">Eliminar pregunta</button>
    </div>
  `,
})
export class NgbdModalConfirm {
  @Output() confirmDelete = new EventEmitter<void>();
  modal = inject(NgbActiveModal);

  confirm() {
    this.confirmDelete.emit();
    this.modal.close('Ok click');
  }
}

export class NgbdModalConfirmAutofocus {
  modal = inject(NgbActiveModal);
}

const MODALS: { [name: string]: Type<any> } = {
  focusFirst: NgbdModalConfirm,
  autofocus: NgbdModalConfirmAutofocus,
};

@Component({
  selector: 'app-questions',
  templateUrl: './questions.component.html',
  styleUrls: ['./questions.component.scss'],
})
export class QuestionsComponent implements OnInit {
  questions: IQuestion[] = [];
  visibleQuestions: IQuestion[] = []; // Array per le domande visibili nella pagina corrente
  totalQuestions: number = 0;
  currentPage: number = 1;
  pageSize: number = 10; // Numero di elementi per pagina
  paginationId: string = 'questionsPagination'; // ID unico per il componente di paginazione
  loading: boolean = false;
  private questionIdToDelete: string = '';

  constructor(private questionService: QuestionService,
              private router: Router,
              private modalService: NgbModal) {}

  ngOnInit() {
    this.getAllQuestions();
  }

  getAllQuestions() {
    this.loading = true;
    this.questionService.getQuestions().snapshotChanges().subscribe({
      next: (data) => {
        this.questions = [];
        this.totalQuestions = 0;
        data.forEach((item: IQuestion | any) => {
          let question = item.payload.toJSON() as IQuestion;
          question.key = item.key;
          this.questions.push(question);
          this.totalQuestions++;
        });
        this.loading = false;
        this.updateVisibleQuestions(); // Aggiorna le domande visibili
      },
      error: (error) => {
        console.error("Error loading questions: ", error);
        this.loading = false;
      }
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.updateVisibleQuestions();
  }

  updateVisibleQuestions() {
    const startIndex = (this.currentPage - 1) * this.pageSize;
    const endIndex = startIndex + this.pageSize;
    this.visibleQuestions = this.questions.slice(startIndex, endIndex);
  }

  sortBy(key: string) {
    this.questions = orderBy(this.questions, [key]);
    this.updateVisibleQuestions();
  }

  open(name: string, questionId: string) {
    const modalRef = this.modalService.open(MODALS[name]);
    this.questionIdToDelete = questionId;
    modalRef.componentInstance.confirmDelete.subscribe(() => this.deleteQuestion());
  }

  deleteQuestion() {
    this.questionService.deleteQuestion(this.questionIdToDelete).then(() => {
      this.loading = true;
      this.reloadCurrentRoute();
    }).catch((error) => {
      console.error("Error deleting question: ", error);
    });
  }

  reloadCurrentRoute() {
    const currentUrl = this.router.url;
    this.router.navigateByUrl('/', { skipLocationChange: true }).then(() => {
      this.router.navigate([currentUrl]);
    });
  }


  nextPage() {
    if (this.currentPage < this.getTotalPages()) {
      this.currentPage++;
      this.updateVisibleQuestions();
    }
  }

  prevPage() {
    if (this.currentPage > 1) {
      this.currentPage--;
      this.updateVisibleQuestions();
    }
  }

  getTotalPages(): number {
    return Math.ceil(this.totalQuestions / this.pageSize);
  }


}
